






import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ChatService } from "@/x-vue/services/chat/chat.service";
import { ChatRoomEnter, ChatRoomService } from "@/x-vue/services/chat/chat.room.service";

import ChatMessageList from "@/x-vue/components/chat/ChatMessageList.vue";
import { ApiService } from "@/x-vue/services/api.service";
import { AppService } from "@/service/app.service";

@Component({
  components: {
    ChatMessageList,
  },
})
export default class ChatUserRoomView extends Vue {
  chat = ChatService.instance;

  mounted(): void {
    // console.log("ChatUserRoomView::ChatMessageList; ");
    this.enter();
  }

  async enter(): Promise<void> {
    const roomInfo: ChatRoomEnter = {
      displayName: AppService.instance.user.displayName,
    };
    if (this.$route.query.id != null) {
      roomInfo.id = "" + this.$route.query.id;
    } else if (this.$route.query.uid) {
      roomInfo.users = (this.$route.query.uid as string).split(",");
    } else {
      return AppService.instance.alert("Missing params", "missing id or users firebaseUid");
    }

    if (this.$route.query.hatch != null) {
      roomInfo.hatch = this.$route.query.hatch == "true" ? true : false;
    }

    try {
      await ChatRoomService.instance.enter(roomInfo);
      if (this.chat.room.global.otherUserId && this.chat.room.isModerator) {
        const user = await ApiService.instance.otherUserProfile(this.chat.room.global.otherUserId);
        const usersInfo: Record<string, unknown> = {
          [user.firebaseUid]: {
            photoUrl: user.photoUrl,
            displayName: user.nicknameOrName,
          },
          [this.chat.room.loginUserUid]: {
            photoUrl: ApiService.instance._user.photoUrl,
            displayName: ApiService.instance._user.nicknameOrName,
          },
        };
        this.chat.room.updateGlobalRoomUsersInfo(usersInfo);
      }
    } catch (e) {
      this.$app.error(e);
    }
  }
}
